<template>
    <div class="payment">
        <HeaderComp></HeaderComp>
        <div class="payment-success">
            <span style="font-weight: bold; font-size: 1.2em;">Заказ #{{ $route.params.orderid }} успешно оплачен</span>
            <p style="align-self: flex-start">В ближайшее время с вами свяжутся для уточнения деталей</p>
            <h2 style="align-self: flex-start; font-weight: bold;">Детали заказа</h2>
            <div class="cart_table">
                <div class="cart_elem" v-for="prod of prods" :key="prod.id">
                    <div class="cart_elem_image">
                        <img :src="prod.images[0].path" height="100" v-if="prod.images.length > 0">
                        <img src="@/assets/icons/product_none.png" height="100" v-else>
                    </div>
                    <div class="cart_elem_meta_group">
                        <span class="cart_elem_name">{{ prod.name }}</span>
                        <span class="cart_elem_prop">Размер: {{ $Helper.findSizeNameByID(prod,prod.size)}}</span>
                        <span class="cart_elem_prop" v-for="meta in JSON.parse(prod.meta)" :key="meta.name">{{$Helper.translateMetaKey(meta.name)}}: {{ meta.value }}</span>
                        <span class="cart_elem_prop">Количество: {{ prod.amount }} шт</span>
                    </div>
                    <div class="cart_elem_meta_group">
                        <div class="cart_elem_meta">
                            <span class="cart_elem_price">{{ ~~prod.price * ~~prod.amount }} р.</span>
                        </div>
                    </div>
                </div>
            </div> 
            <span style="align-self: flex-start; font-weight: bold;">Общая стоимость заказа {{ summ }}р.</span>
        </div>
    </div>
</template>
<script>
import HeaderComp from '@/components/HeaderComp.vue';


export default {
    name : "payment-success",
    components: {
        HeaderComp
    },
    data(){
        return {
            prods : [],
            checked : 0,
            summ : 0
        }
    },
    mounted(){
        console.log(this.$route.params.orderid);
        this.$CoreAPI.get({
            action : "get_order",
            addToURl : `&order_id=${~~this.$route.params.orderid}`
        }).then((result)=>{
            this.prods = result.prods;
            this.checked = JSON.parse(result.checked);
            this.summ = result.summ;
            console.log(result)
        })
    }
}
</script>
<style scoped>
@import url("@/assets/styles/__cart.scss");
    .payment-success{
        margin: 0 auto;
        width: 600px;
        display: flex;
        align-items: center;
        flex-direction: column;
        row-gap: 1vh;
        padding: 20px;
        background: #eaeaea;
    }
    .payment{
        width: 100vw;
    }
    

</style>